* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@import url("https://fonts.googleapis.com/css?family=Raleway:400,400i,700");

h1 {
  font-family: Raleway, sans-serif;
  color: slategray;
  margin-top: 2vh;
}

h3 {
  color: slategray;
}

.app {
  text-align: center;
  background-color: rgb(10, 48, 60);
  min-height: 100vh;
  min-width: 100vw;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: whitesmoke;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.wrapper {
  background-color: #051a21;
  border-radius: 20px;
  width: 80vw;
  height: 70vh;
  display: flex;
  flex-direction: column;
  display: flex;
  align-items: center;
  box-shadow: 0 0 8px 4px hsl(200, 100%, 50%);
}

.session {
  width: 100%;
  height: 10vh;
  color: hsl(200, 100%, 50%)
}


.changeMinutes {
  margin: 4% 8%;
  padding: 0;
  border: none;
  background: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 25px;
  background-color: hsl(200, 100%, 50%);
}

.changeMinutes:hover {
  background-color: hsl(200, 100%, 44%)
}

.changeMinutes:focus {
  outline-width: 0;
}



.time-inactive {
  color: lightgrey;
  font-size: 6rem;
  padding: 2rem;
  display: flex;
}

.time-active {
  font-size: 6rem;
  padding: 2rem;
  display: flex;
  color: orange;
}

.row {
  margin-top: 2vh;
}

.button {
  padding: .6rem 1.5rem;
  margin: .4rem;
  border-radius: 6px;
  letter-spacing: 1px;
  font-weight: 600;
  font-family: Raleway, sans-serif;
  font-size: 1rem;
  border-style: groove;
  background-color: cadetblue;
  align-items: center;
  color: rgb(102, 0, 0);
}

.button:focus {
  outline-width: 0;
}


.button-primary-active {
  border: 1px solid beige;
  background-color: #74896f;
}

.button-primary-active:hover {
  border: 1px solid beige;
  background-color: #7d9883;
}

.button-primary-inactive {
  background-color: #567258;
  border: 1px solid beige;
}

.button-primary-inactive:hover {
  border: 1px solid beige;
  background-color: #576d53;
}


.button-res {
  padding: .6rem 1.5rem;
  background-color: darkkhaki;
  margin: .4rem;
  border-radius: 6px;
  font-weight: 600;
  font-family: Raleway, sans-serif;
  font-size: 1rem;
  border-style: groove;
  letter-spacing: 1px;
  color: hsl(200, 100%, 16%);
}

.button-res:hover {
  background-color: whitesmoke;
}